.theater-overlay {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 9998;
  background: rgba(0, 0, 0, 0.5);
}

.theater-container {
  position: fixed;
  top: 40px;
  left: 50%;
  z-index: 9999;
  visibility: hidden;
  opacity: 0;
  transition: visibility 300ms linear, opacity 300ms linear;
  transform: translateX(-50%);
}

.theater-container.shown {
  visibility: visible;
  opacity: 1;
}
